.activity {
  max-width: 1211px !important;
  display: flex;
  justify-content: space-between;
  &__card {
    text-align: center;
  }
}
.card {
  max-width: 362px;
  color: #333333;
  padding: 40px 0 20px 0;
  &__image {
    display: inline-block;
    padding: 16px;
    margin: 0 0 15px 0;
    background: #28a745;
    border-radius: 33.5px;
  }
  &__title {
    margin: 0 0 18px 0;
    font-size: 20px;
  }
  &__desc {
    line-height: 28px;
  }
}

@media (max-width: 1124.98px) {
  .activity {
    justify-content: space-around;
  }
  .card {
    max-width: 219px;
  }
}

@media (max-width: 768.98px) {
  .card {
    max-width: 185px;
    &__title {
      font-size: 18px;
    }
    &__desc {
      font-size: 16px;
    }
  }
}
@media (max-width: 600.98px) {
  .activity {
    flex-direction: column;
    margin: 30px 0 !important;
  }
  .card {
    max-width: 100%;
    &:not(:last-child) {
      padding: 0 0 18px 0;
    }
    &:last-child {
      padding-bottom: 0;
    }
    &__title {
      margin: 0 0 15px 0;
    }
  }
}
