.callback {
  &__wrapper {
    display: flex;
    // align-items: center;
  }
  margin: 59px 0 0 0;
  &__img {
    flex: 1 1 50%;
    overflow: hidden;
    img {
      height: 100%;
      width: 100%;
      object-fit: cover;
      object-position: 50% 50%;
    }
  }
  &__request {
    flex: 1 1 50%;
    padding: 78px 0 60px 30px;
    form {
      display: flex;
      flex-direction: column;
      flex-wrap: wrap;
      input {
        width: 100%;
        display: block;
        color: #808080;
        border: none;
        outline: 1px solid #ccc;
        padding: 8px 0 8px 5px;
        &:not(:last-child) {
          margin: 0 0 36px 0;
        }
      }
      ::placeholder {
        padding: 0 0 0 5px;
        color: #808080;
      }
      button {
        cursor: pointer;
        margin: 53px 0 0 0;
        padding: 7px 0 10px 0;
        width: 100%;
        background: #28a745;
        border-radius: 5px;
        color: #ffffff;
        border: none;
        &:hover {
          background: #41b75c;
          transition: background 0.25s ease-in-out;
        }
        &:active {
          opacity: 0.9;
          transition: opacity 0.25s ease-in-out;
        }
      }
    }
  }
  .request {
    &__title {
      font-weight: 300;
      font-size: 38px;
      margin: 0 0 34px 0;
    }
  }
}

.callback__container {
  padding: 0 20px 0 0;
}

@media (max-width: 976.98px) {
  .callback__img {
    img {
      width: 384px;
      height: 100%;
    }
  }
  .callback__request {
    padding: 26px 0 26px 30px;
    form {
      button {
        margin: 27px 0 30px 0;
      }
    }
  }
}

@media (max-width: 690.98px) {
  .callback {
    &__wrapper {
      flex-direction: column;
    }
    &__request {
      padding: 0 0 0 20px;
    }
    &__img {
      margin: 0 0 26px 0;
      img {
        width: 100%;
        height: 100%;
      }
    }
  }
  form {
    input {
      &:not(:last-child) {
        margin: 0 0 26px 0 !important;
      }
    }
    button {
      margin: 10px 0 30px 0 !important;
    }
  }
}
