.header {
  background-image: url("../../../public/img/header-background.png");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  &__container {
    max-width: 1314px;
  }
  &__navigation {
    padding: 6px 0;
    display: flex;
    align-items: center;
    justify-content: space-between;
  }
  &__logo {
    cursor: pointer;
    display: flex;
    align-items: center;
  }
  &__menu {
    display: flex;
    align-items: center;
    color: #fff;
    cursor: pointer;
  }
  &__content {
  }
}
.logo {
  font-size: 35px;
  &__img {
    margin: 0 10px 0 0;
  }
  &__title {
    color: #28a745;
    span {
      color: #fff;
    }
  }
}
.menu {
  &__item {
    &:hover {
      color: #41b75c;
      transition: color 0.25s ease-in-out;
    }
    &:active {
      opacity: 0.8;
      transition: opacity 0.25s ease-in-out;
    }
    font-size: 18px;
    &:not(:last-child) {
      margin: 0 38px 0 0;
    }
  }
}
.content {
  padding: 205px 0 280px 0;
  max-width: 493px;
  text-align: center;
  margin: 0 auto;
  color: #e9e9e9;
  &__title {
    font-weight: 300;
    font-size: 55px;
    margin: 0 0 29px 0;
  }
  &__desc {
    font-size: 22px;
    margin: 0 0 29px 0;
  }
  &__button {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    padding: 12px 30px;
    background: #28a745;
    border-radius: 5px;
    svg {
      margin: 0 10px 0 0;
    }
    &:hover {
      background: #41b75c;
      transition: background 0.25s ease-in-out;
    }
    &:active {
      opacity: 0.9;
      transition: opacity 0.25s ease-in-out;
      span {
        opacity: 0.9;
      }
    }
  }
}
@media (max-width: 768.98px) {
  .header {
    &__content {
      padding: 44px 0 95px 0;
    }
    &__navigation {
      flex-direction: column;
      flex-wrap: wrap;
      justify-content: center;
    }
  }
  .logo__title {
    font-size: 32px;
  }
  .menu {
    justify-content: center;
    flex-wrap: wrap;
    &__item {
      &:not(:last-child) {
        margin: 0 12px 0 0;
      }
    }
  }
  .content__title {
    line-height: 66px;
  }
  .content__desc {
    line-height: 37px;
  }
}

@media (max-width: 340.98px) {
  .content__title {
    font-size: 52px;
    margin: 0 0 16px 0;
  }
  .content__desc {
    font-size: 22px;
    margin: 0 0 27px 0;
  }
  .menu {
    &__item {
      font-size: 16px;
    }
  }
  .logo__title {
    font-size: 27px;
  }
}
