.WeDo {
  margin: 0 0 41px 0;
  &__info {
    text-align: center;
  }
  &__container {
    max-width: 1304px;
  }
  .info {
    &__title {
      font-weight: 400;
      font-size: 20px;
      line-height: 33px;
      border-bottom: 1px solid #000000;
      margin: 0 0 8px 0;
    }
    &__subTitle {
      font-weight: 300;
      font-size: 38px;
      margin: 0 0 34px 0;
    }
    &__desc {
      margin: 0 0 39px 0;
    }
  }
  &__image {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    img {
      margin: 0 0 7px 0;
      max-width: 320px;
    }
  }
}

@media (max-width: 1024px) {
  .info {
    &__title {
      border-bottom: none;
    }
  }
  .WeDo__image {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    grid-template-rows: repeat(2, 130px);
    img {
      width: 172px;
    }
  }
}

@media (max-width: 748px) {
  .WeDo__image {
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-direction: column;
    img {
      width: 100%;
      max-width: initial;
    }
  }
}
