.solutions {
  &__wrapper {
    display: flex;
    margin: 0 0 50px 0;
    &_img_768 {
      display: none;
    }
  }
  &__images {
    flex: 1 1 50%;
    img {
      width: 100%;
      height: 100%;
    }
  }

  color: #fff;
  .card {
    flex: 1 1 50%;
    padding: 92px 44px 92px 30px;
    background: #28a745;
    &__title {
      font-size: 20px;
      font-weight: 400;
      margin: 0 0 10px 0;
    }
    &__subTitle {
      font-weight: 300;
      font-size: 38px;
      margin: 0 0 26px 0;
    }
    &__desc {
      font-size: 16px;
      line-height: 28px;
      margin: 0 0 17px 0;
    }
    &__button {
      &:hover {
        background: #41b75c;
        transition: background 0.25s ease-in-out;
      }
      &:active {
        background: #36944c;
        transition: background 0.25s ease-in-out;
      }
    }
  }
}

@media (max-width: 1360px) {
}
@media (max-width: 1360px) {
  .card {
    padding: 30px !important;
  }
}
@media (max-width: 1048px) {
  .solutions__wrapper_img_1360 {
    display: none;
  }
  .solutions__wrapper_img_768 {
    // height: 100vh;
    display: block;
  }
}

@media (max-width: 768px) {
  .solutions__wrapper {
    flex-direction: column;
  }
  .solutions__wrapper_img_768 {
    display: none;
  }
  .solutions__wrapper_img_1360 {
    display: block;
  }
  .card {
    &__title {
      font-size: 18px !important;
      font-weight: 400 !important;
    }
    &__subTitle {
      font-weight: 300;
      font-size: 36px !important;
    }
    &__desc {
      font-size: 16px !important;
      line-height: 26px !important;
    }
  }
}
