.team {
  &__container {
    max-width: 1304px !important;
  }
  &__info {
    text-align: center;
  }
  .info {
    &__title {
      margin: 0 0 16px 0;
      font-weight: 400;
      font-size: 20px;
    }
    &__subTitle {
      margin: 0 0 34px 0;
      font-weight: 300;
      font-size: 38px;
    }
    &__desc {
      margin: 0 0 45px 0;
    }
  }
  &__person {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    cursor: pointer;
  }
  .person {
    &__wrapper {
      text-align: center;
      &_img {
        position: relative;
        &:hover {
          scale: 1.05;
          transition: scale 0.25s ease-in-out;
          img {
            filter: brightness(40%);
            transition: filter 0.25s ease-in-out;
            box-shadow: 2px 2px 12px 2px rgba(0, 0, 0, 0.4);
          }
          // .social {
          //   filter: none;
          //   display: block;
          // }
        }
        .social {
          position: absolute;
          top: 43%;
          left: 0;
          right: 0;
          background-size: 100%;
          a:not(:last-child) {
            margin: 0 25px 0 0;
          }
        }
      }
      &_name {
        margin: 13px 0 0 0;
        font-size: 25px;
      }
      &_job {
        margin: 12px 0 0 0;
      }
    }
  }
}
.socialHover {
}

@media (max-width: 1304.98px) {
  .person__wrapper_img {
    img {
      width: 298px;
      height: 208px;
    }
  }
}
@media (max-width: 942.98px) {
  .person__wrapper_img {
    img {
      width: 223px;
      height: 148px;
    }
  }
}
@media (max-width: 720.98px) {
  .team__person {
    flex-direction: column;
  }
  .person__wrapper:not(:last-child) {
    margin: 0 0 10px 0;
  }
  .person__wrapper_img {
    img {
      width: 420px;
      height: 280px;
    }
  }
}
@media (max-width: 458.98px) {
  .info__title {
    font-size: 18px;
  }
  .info__subTitle {
    font-size: 38px;
  }
  .info__desc {
    font-size: 16px;
    line-height: 26px;
  }
  .person__wrapper_img {
    img {
      width: 100%;
      height: 100%;
    }
  }
}
