body {
  font-family: "Open Sans", sans-serif;
  font-weight: 400;
  font-size: 18px;
  color: #333333;
}
a {
  text-decoration: transparent;
}

ul {
  padding-inline-start: 0;
}
li {
  list-style: none;
}

*,
*:before,
*:after {
  box-sizing: border-box;
}

h1,
h2,
h3,
p,
figure {
  margin: 0;
}

