.blog {
  margin: 0 0 50px 0;
  &__wrapper {
    display: flex;
    justify-content: space-between;
    padding: 0 20px 0 0;
  }
  &__img {
    img {
      width: 100%;
      height: 100%;
    }
  }
  &__info {
    color: #fff;
    background: #0284d0;
    min-height: 292px;
    padding: 112px 77px 113px 30px;
  }
  .info {
    &__title {
      font-weight: 400;
      font-size: 20px;
      margin: 0 0 5px 0;
    }
    &__subTitle {
      font-weight: 300;
      font-size: 38px;
      margin: 0 0 26px 0;
    }
    &__desc {
      max-width: 606px;
      line-height: 28px;
      margin: 0 0 17px 0;
    }
    .currentButton {
      &:hover {
        background: #1177b3;
        transition: background 0.25s ease-in-out;
      }
      &:active {
        background: #0d5a86;
        transition: background 0.25s ease-in-out;
      }
    }
  }
}

@media (max-width: 1187px) {
  .blog {
    &__info {
      padding: 29px 45px 30px 30px;
    }
    &__img {
      img {
        object-fit: cover;
        object-position: 50% 50%;
      }
    }
  }
}

@media (max-width: 680px) {
  .blog {
    &__wrapper {
      flex-direction: column-reverse;
    }
    &__info {
      margin: -4px 0 0 0;
    }
  }
  .info {
    &__title {
      font-size: 18px !important;
    }
    &__subTitle {
      font-size: 38px !important;
    }
    &__desc {
      font-size: 16px !important;
    }
  }
}
@media (max-width: 320px) {
  .blog__info {
    padding: 29px 10px 30px 10px;
  }
}
