.footer {
  padding: 20px 0;
  background: #333333;
  &__wrapper {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  &__social {
    display: flex;
    align-items: center;
    a {
      &:hover {
        scale: 1.1;
        transition: scale 0.25s ease-in-out;
      }
    }
    a:not(:last-child) {
      margin: 0 25px 0 0;
    }
    a:nth-child(2) {
      transform: translateY(2px);
    }
    a:nth-child(3) {
      transform: translateY(5px);
    }
  }
  &__design {
    margin: 10px 0 0 0;
    color: #fff;
    text-align: center;
  }
}
